



























import {Component, Vue} from 'vue-property-decorator';
import {Business} from '../../model/business_model';
import {Divider} from 'vant';
import 'vant/lib/divider/style';

Vue.use(Divider);
@Component
export default class BusinessDetail extends Vue {
    private businessModel: Business = new Business();

    public created(): void {
        this.$store.state.title = '业务员详情';
    }

    public mounted(): void {
        this.businessModel = this.$store.state.nowBusiness;
    }
}
